   /* RESET */
   *{padding: 0;margin: 0; color: #333;}
   li{list-style: none;}
   a{text-decoration: none;}
   a:hover{color: red;}
   /* COMMON */
   .container{max-width: 1200px; margin: 0 auto;}
   .rel{position: relative;}
   .abs{position: absolute;}
   .d-flex{display: flex;}
   .d-block{display: block;}
   .jcb{justify-content: space-between;}
   .aic{align-items: center;}
   .aie{align-items: end;}
   .ofh{overflow: hidden;}
   .h-0{height: 0;}
   .h-100px{height: 100px;}
   .h-200px{height: 200px;}
   .h-300px{height: 300px;}
   .z-1{z-index: 1;}
   .z-2{z-index: 2;}
   .z-3{z-index: 3;}
   .w-100{width: 100%;}
   .mx-auto{margin: 0 auto;}
   .text-center{text-align: center;}
   .bg-check{background-color: gray;} /*레이아웃 체크용*/
   .bg-blue{background-color: blue;} /*레이아웃 체크용*/
   
   /* Layout */
   #hd_hoverall #gnb::before{ 
     content: "";
     position: absolute;
     left: 0;
     right: 0;
     top: 100%;
     background-color: skyblue;
     height: 0px;
     transition: .5s;
   }
   #hd_hoverall #gnb:hover::before{
     height: 150px;
   }
   #hd_hoverall #gnb:hover .d2ul{
     height: 150px;
   }
   
   #hd_hoverall #gnb .d1li:hover{
     background-color: skyblue;
   }
   
   #hd_hoverall .d1a{
     line-height: 100%;
     font-size: 1.8rem;
     padding: 0 2rem;  
   }
   
   #hd_hoverall .d2ul{
     top: 100%;
     min-width: 110px;
     height: 0;
     overflow: hidden;
     transition: .5s;
   }
   
   #hd_hoverall #gnb .d2ul li:hover{
     background-color: slateblue;
   }